:root {
    --backdrop-visible-background: var(--color-light-bg-overlay);
    --backdrop-hidden-background: transparent;
}
.backdrop__backdrop_2p5hs {
    z-index: -1;
    position: fixed;
    right: 0;
    bottom: 0;
    top: 0;
    left: 0;
    -webkit-tap-highlight-color: transparent; /* убирает хайлайт */
}
.backdrop__appear_2p5hs,
.backdrop__enter_2p5hs {
    background-color: var(--backdrop-hidden-background);
}
.backdrop__appearActive_2p5hs,
.backdrop__enterActive_2p5hs,
.backdrop__appearDone_2p5hs,
.backdrop__enterDone_2p5hs {
    background-color: var(--backdrop-visible-background);
    transition: background-color 200ms ease-in;
}
.backdrop__exit_2p5hs {
    background-color: var(--backdrop-visible-background);
}
.backdrop__exitActive_2p5hs,
.backdrop__exitDone_2p5hs {
    background-color: var(--backdrop-hidden-background);
    transition: background-color 200ms ease-out;
}
.backdrop__invisible_2p5hs {
    opacity: 0;
}
